<template>
    <div>
        <u-button size="large" shape="circle" @on-click="onClicked">{{opText}}</u-button>
        <u-clip width="26px" height="37px" :skin="bitImg" mouseEnable="true" clips="3,1,1" :frame="frame"></u-clip>
        <u-clip width="26px" height="100px" :skin="bitImg" mouseEnable="true" clips="3,1,1" grids="0,16,0,19" :frame="frame"></u-clip>
        <u-clip width="100px" height="100px" :skin="bitImg" mouseEnable="true" clips="3,1,1" grids="12,16,12,19" :frame="frame"></u-clip>
        <br/>
        <br/>
        <br/>
        <u-clip width="10px" height="14px" :skin="numsImg1" mouseEnable="true" clips="1,10" :frame="numIndex"></u-clip>
        <u-clip width="10px" height="14px" :skin="numsImg2" mouseEnable="true" clips="1,10" :frame="numIndex"></u-clip>
        <u-clip width="37px" height="48px" :skin="webpImg" mouseEnable="true" clips="56,1" :frame="webpIndex"></u-clip>
    </div>
</template>
<script>
    import img from './img/barragefullscreen_btn.png';
    import numsImg1 from './img/carddating/num_blue.png';
    import numsImg2 from './img/carddating/num_red.png';
    import webpImg from './img/noble_head.webp';
    export default {
        data () {
            return {
                bitImg: img,
                frame: 0,
                numIndex: 0,
                webpIndex: 0,
                numsImg1: numsImg1,
                numsImg2: numsImg2,
                opText: '开始改变切片',
                timerId: null,
                webpImg
            };
        },
        mounted() {

        },
        methods: {
            onClicked() {
                const isRunning = this.timerId;
                if (isRunning) {
                    this.$Core.App.timers().clearTimer(this.timerId);
                    this.timerId = null;
                }
                this.opText = (isRunning && '开始改变切片') || '停止改变切片';

                !isRunning && (this.timerId = this.$Core.App.timers().doLoop(500, this.onNextTick));
            },
            onNextTick() {
                this.frame = ++this.frame % 2;
                this.webpIndex = ++this.webpIndex % 56;
                this.numIndex = ++this.numIndex % 10;
            },
        }
    };
</script>
<style>
</style>



